<template>
  
  <div id="task-flow-container" 
    :updateChart="!updateChartComputed"
    :style="{ height: options.height + 'px' }"
  >
    <!-- 
      virtualValue
      computed
     -->
    
  </div>
</template>


<script>
import * as echarts from 'echarts';
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'FlowChart',
  props: [
    'options',
    'rootSelect',
  ],
  data() {
    return {
      $echarts: null,
      historyFlowJSON: '',
    }
  },
  computed: {
    ...mapGetters('task',[
      'activeTaskRawItem',
    ]),
    chartNode(){
      if(!this.activeTaskRawItem) return {};
      return this.genTaskFlowNodeData(this.activeTaskRawItem);
    },
    linksData(){
      let selected = this.rootSelect.selected || 0;
      if(!this.chartNode[selected]) return [];
      return this.chartNode[selected].links;
    },
    nodesData(){
      let selected = this.rootSelect.selected || 0;
      if(!this.chartNode[selected]) return [];  
      return this.chartNode[selected].data;
    },
    chartsOption(){
      return {
          title: {
              text: ''
          },
          tooltip: {},
          animationDurationUpdate: 1000,
          animationEasingUpdate: 'quinticInOut',
          series: [
            {
              type: 'graph',
              layout: 'none',
              symbolSize: 50,
              roam: true,
              label: {
                show: true
              },
              edgeSymbol: ['circle', 'arrow'],
              edgeSymbolSize: [0, 8],
              edgeLabel: {
                fontSize: 20
              },
              data: this.nodesData,
              links: this.linksData,
              lineStyle: {
                opacity: 0.9,
                width: 2,
                curveness: 0
              }
            }
          ]
      };
    },
    updateChartComputed(){
      // 
      let rawFlowJSON = this.activeTaskRawItem ? JSON.stringify(this.activeTaskRawItem.rawFlows) : '[]';
      if(this.$echarts && rawFlowJSON != this.historyFlowJSON){
        this.historyFlowJSON = rawFlowJSON;
        this.$echarts.clear();
        this.$echarts.setOption(this.chartsOption);
      }
    },
  },
  watch: {
    activeTaskRawItem(){
      // 
      this.$echarts.resize()
    },
    options: {
      immediate: true,
      handler(val){
        this.updateChartForce();
      }
    },
    ['options.height'](){
      this.updateChartForce();
    }
  },
  methods: {
    updateChartForce(){
      this.$nextTick(() => {
        // 
        this.$echarts.clear();
        this.$echarts.setOption(this.chartsOption)
        this.$echarts.resize()
      })
    },
    genParallelLink(linksData, nodesData, o, source){
      // 
      // 
      let nextSources = [];
      // XX
      let x = source.x;
      let y = source.y;
      let mX = x;
      for(let v of o){
        let src = source.val;
        if(Array.isArray(v)){
          let tmpSource = {
            val: src, 
            x: x, 
            y: y,
            mX: x,
            mY: y
          }
          nextSources.push(...this.genSerialLink(linksData, nodesData, v, tmpSource))
          // YY
          y = tmpSource.mY;
          // XX
          mX = Math.max(mX, tmpSource.mX)
        }else{
          // // 
          // let [a, b, ...c3g] = v.split('-')
          // v = c3g.join('-')

          linksData.push({source: src, target: v})
          nextSources.push(v)

          // 
          nodesData.push({
            name: v,
            x: x,
            y: y,
          })
        }
        // Y
        y++;
      }
      // y1y++
      o != [] && y--;
      // XY
      source.mX = mX;
      source.mY = y;
      return nextSources;
    },
    genSerialLink(linksData, nodesData, o, source){
      // 
      // 
      let nextSources = null;
      let x = source.x;
      let y = source.y;
      // Y
      let mY = source.y;
      for(let v of o){
        // 
        let src = nextSources || source.val;

        if(Array.isArray(v)){
          let tmpSource = {
            val: src, 
            x: x, 
            y: y,
            mX: x,
            mY: y
          };
          // 
          nextSources = this.genParallelLink(linksData, nodesData, v, tmpSource)
          // XX
          x = tmpSource.mX;
          // YY
          mY = Math.max(tmpSource.mY, mY);
        }else{
          // // 
          // let [a, b, ...c3g] = v.split('-')
          // v = c3g.join('-')

          // src
          if(Array.isArray(src)){
            for(let i of src){
              linksData.push({source: i, target: v})
            }
          }else{
            linksData.push({source: src, target: v})
          }
          // 
          // 
          nextSources = [v]
          
          // 
          nodesData.push({
            name: v,
            x: x,
            y: y,
          })
        }
        // X
        x+=2;
      }
      // x1x++
      o != [] && (x-=2);
      // XY
      source.mY = mY;
      source.mX = x;
      return nextSources;
    },
    genTaskFlowNodeData(rawItem){
      let rawFlows = rawItem.rawFlows;
      let rootNodesData = {};
      for(let index in rawFlows){
        // 
        let data = [{
          name: index,
          x: -2,
          y: 0
        }];
        let links = [];
        rootNodesData[index] = {
          data: data,
          links: links,
        }
        let source = {
          val: index,
          x: 0,
          y: parseInt(index),
        }

        this.genParallelLink(links, data, rawFlows[index], source)
      }
      return rootNodesData
    },
    
  },
  mounted(){
    let dom = document.getElementById('task-flow-container');
    let charts = echarts.init(dom);
    this.$echarts = charts;
    // window.testUpdate = () => {
    //   this.$echarts.setOption(this.chartsOption);
    //   this.$echarts.resize()
    // };
  },
}
</script>


<style lang='stylus' scoped>


#task-flow-container
  >>> div:first-child
  >>> div:first-child canvas
    height: 100% !important
    width: 100% !important

</style>


